@import '../scss/_mixins&includes.scss';

/* transition */
.ui-loader {
  display: none !important;
}
.transition-all,
a,
.tr,
[class^="btn-"],
[class*=" btn-"],
.wrap-input  {
  transition:all .5s ease-in-out;
}
/* transition end */
/* wrapper style */
.wrapper {
  position: relative;
  //height: 100vh;
  max-width: 1180px;
  min-width: 320px;
  margin: 0 auto;
  overflow-x: hidden;
  height: 150vh;
  &.welcome {
    height: 100vh;
    max-width: 100%;

  }

  &-inner {
    //overflow: hidden;
    width: 100%;
    @extend .cfx;
    //overflow-x: hidden;
  }

}
/* container style */
.container {
  //width: 1180px;
  max-width: 1180px;
  min-width: 320px;
  position: relative;
  padding: 0 15px;
  margin: 0 auto;
  @extend .clearfix;
  z-index: 10;
  //padding: 0 50px;
}

.header {
  @extend .clearfix;
  position: relative;
  padding: 15px 0;
  background: #4c4c56;
  //position: fixed;
  width: 100%;
  z-index: 98;
  .container {
    position: static;
  }
}

.header-logo-holder {
  overflow: hidden;
  float: left;
}



.welcome-holder {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background: #4d4d51 url('../img/body-bg.jpg');
  background-size: cover;
  text-align: center;
  z-index: 100;
  &.hidden {
    display: none;
  }
  span {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}


.user-choise-holder {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background: #4d4d51 url('../img/body-bg.jpg');
  background-size: cover;
  //position: relative;
  .container {
    padding: 0 50px;
    position: absolute;
    left: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }
  &.hidden {
    display: none;
  }
}



