.user-list {
  padding: 0;
  margin: 0;
  list-style: none;
  transition:all .5s ease-in-out;
  li {
    position: absolute;
    left: 50%;
    top: 50%;
    transition:all .6s ease-in-out;
    opacity: 0;
    visibility: hidden;
    transform: translate(-50%, -50%);
    width: 35px;
    height: 35px;
    background: #9894ff !important;
    text-align: center;
    border-radius: 50%;
    a {
      display: block;
      position: relative;
      width: 35px;
      height: 35px;
    }
    span {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    &:before {
      content: none;
    }
  }
}