input[type=text],
input[type=password],
input[type=date],
input[type=datetime],
input[type=email],
input[type=number],
input[type=search],
input[type=tel],
input[type=time],
input[type=url],
select,
textarea {
  display: block;
  border-left: none;
  outline-style: none;
  -webkit-appearance:none;
  @include inputPlaceholder(#8c88ff);
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 2px solid #434442;
  padding: 16px 12px;
  border-radius: 0px;

  text-transform: capitalize;
  color: #8c88ff;
  &:focus {
    box-shadow: none !important;
    border-bottom-color: #8c88ff;
    //border-bottom: $white_color 2px solid !important;
  }
}


select {
  display: block;
  border-left: none;
  outline-style: none;
  -webkit-appearance:none;
  @include inputPlaceholder(#8c88ff);
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 2px solid #434442;
  padding: 16px 12px;
  border-radius: 0px;

  text-transform: capitalize;
  color: #8c88ff;
  background-image: url("../img/select-down.png");
  background-position: 98% 50%;
  background-repeat: no-repeat;
  background-size: 8px 8px;
  &:focus {
    border-bottom-color: #8c88ff;
    background-image: url("../img/select-top.png");
  }
}


.checkbox-row,
.radio-row {
  margin-bottom: 10px;
  &:last-child {
    //margin-bottom: 0;
  }
  label {
    display: inline-block;
    cursor: pointer;
    position: relative;
    padding-left: 25px;
    margin-right: 15px;
    font-size: 13px;
  }
  input[type=radio],
  input[type=checkbox] {
    display: none;
  }

  label:before {
    content: "";
    display: inline-block;

    width: 16px;
    height: 16px;
    font-size: 12px;

    margin-right: 10px;
    position: absolute;
    left: 0;
    bottom: 1px;
    background: #fff;
    color: grey;
    font-family: 'FontAwesome';
  }
  .radio label:before {
    border-radius: 8px;
  }
  input[type=radio]:checked + label:before,
  input[type=checkbox]:checked + label:before {
      content: "\2022";
      background: #fff;
      color: grey;
      font-size: 14px;
      text-align: center;
      line-height: 17px;
  }
}

.radio-row {
  label:before {
    border-radius: 50%;
  }

}

.checkbox-row {
  input[type=checkbox]:checked + label:before {
    font-family: 'FontAwesome';
    content: "\f00c";
  }
}

.link {
  font-size: 0;
  color: #fff;
  span {
    font-size: 16px;
    @extend .inline-m;
  }

  &.disabled {
    pointer-events: none;
  }
  &.like,
  &.comment,
  &.share {
    padding: 5px 20px;
    border-radius: 10px;
    display: block;
    .count {
      padding-left: 5px;
    }
  }
  &.like {
    background: #62d7c8;
  }
  &.comment {
    background: #fcb465;
  }
  &.share {
    background: #ff3366;
  }
  &.more {
    font-size: 16px;
    color: #62d7c8;
    margin-bottom: 10px;
    display: block;
  }
  &.logout {
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 30px;
    display: block;
    border-radius: 50%;
    margin: 15px auto 0;
    position: relative;
    background: #8c88ff;
    span {
      display: block;
      font-size: 20px;
      &:before {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
  &.sorting {
    font-size: 18px;
    padding: 15px 20px;
    border-radius: 10px;
    display: block;
    background: #8c88ff;
    &.active {
      background: #62d7c8;
    }
    &.disabled {
      background: #fcb465;
    }
    &.series {
      &.disabled {
        pointer-events: all;
      }
      &.active {
        background: #62d7c8;
      }
    }
  }
  &.continue,
  &.basket {
    display: block;
    width: 80%;
    margin: 0 auto 25px;
    text-transform: capitalize;
    font-size: 18px;
    padding: 15px 20px;
    border-radius: 10px;
    background: #62d7c8;
  }
}

.btn {
  background: #8c88ff !important;
  text-transform: uppercase;
  border-radius: 5px;
  border: none;
  -webkit-appearance:none;
  width: 100%;
  background: #fff;
  padding: 16px 12px;
  color: #ffffff;
  margin-bottom: 10px;
  &.disabled {
    cursor: not-allowed;
  }
  &.visible {
    display: block !important;
  }
  &.hidden {
    display: none;
  }
  &.menu-open {
    float: left;
    border: none;
    color: #ffffff;
    &.left {
      width: 20px;
      height: 30px;
      position: relative;
      margin: 0;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: .5s ease-in-out;
      -moz-transition: .5s ease-in-out;
      -o-transition: .5s ease-in-out;
      transition: .5s ease-in-out;
      cursor: pointer;
      display: block;
      padding: 0;
      display: block;
      background: transparent !important;
      span {
        display: block;
        position: absolute;
        height: 3px;
        width: 100%;
        background: #fff;
        border-radius: 0px;
        opacity: 1;
        left: 0;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;
        &:nth-child(1) {
          top: 0px;
        }
        &:nth-child(2) {
          top: 6px;
        }
        &:nth-child(3) {
          top: 13px;
        }
      }
    }
    &.active {
      span {
        &:nth-child(1) {
          top: 9px;
          -webkit-transform: rotate(135deg);
          -moz-transform: rotate(135deg);
          -o-transform: rotate(135deg);
          transform: rotate(135deg);
        }
        &:nth-child(2) {
          opacity: 0;
          left: -60px;
        }
        &:nth-child(3) {
          top: 9px;
          -webkit-transform: rotate(-135deg);
          -moz-transform: rotate(-135deg);
          -o-transform: rotate(-135deg);
          transform: rotate(-135deg);
        }
      }
    }
  }
  &.review {
    &:hover,
    &:focus {
      background: #62d7c8 !important;
    }
  }
  &.send {
    background: #62d7c8 !important;
  }
  &.add {
    margin-top: 15px;
    font-size: 16px;
    width: 100%;
    line-height: 16px;
    text-transform: capitalize;
  }
  &.comment {
    position: fixed;
    bottom: 0;
    text-align: center;
    width: 250px;
    right: 15px;
    z-index: 10;
  }
  &.about {
    width: 250px;
    float: right;
    line-height: 20px;
    &.hidden {
      display: none;
    }
    &.visible {
      display: block;
    }
  }
}

label {
  display: block;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
  &.error {
