  padding: 50px 0;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 101;
  background: #4c4c56;
  &.hidden {
    display: none;
  }
  &.visible {
    display: block;
  }
  .btn {
    &.finish {
      //display: none;
    }
  }
}

.wrap-form {
  &:not(.login) {
    .form-row {
      &:nth-last-child(2) {
        width: 100%;
      }
    }
  }
  &.reg {
    &.first,
    &.master,
    &.company,
    &.provider {
      background: #4c4c56;
      &.visible {
        display: block;
      }
      &.hidden {
        display: none;
      }
    }
    &.first {
      display: block;
    }

    &.master,
    &.company,
    &.provider {
      display: none;
    }
  }
}


.form-row {
  margin-bottom: 10px;
  position: relative;
