.form {
  &.comment {
    display: none;
    position: fixed;
    right: 0;
    left: 0;
    //top: 0px;
    bottom: 0;
    margin: -15px 0;
    z-index: 100;
    background: #4c4c56;
    padding: 35px 25px;
    //height: 100vh;
    &.visible {
      display: block;
    }
  }
  .btn.send {
    float: right;
    width: 250px;
  }
  .close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
  }
  &.user-edit {
    .row {
      &.edit {
        //display: none;
      }
      &.visible {
        display: block;
      }
      &.hidden {
        display: none;
      }
    }
    .user-top-list {
      .input {
        font-size: 16px;
        text-align: center;
      }
    }
    .user-bottom-list {
      li {
        padding:20px 0;
      }
      .input,
      select {
        width: 200px;
      }
    }
  }
  &.basket {
    display: none;
    &.visible {
      display: block;
    }
    p {
      padding: 15px 0;
      font-size: 18px;
      text-align: center;
    }
    .form-row {
      label,
      span {
        text-transform: capitalize;
      }
    }
    .radio-row {
      label {
        font-size: 16px;
      }
    }
    .btn {
      width: 200px;
      float: right;

    }
  }
  &.post.comment {
    position: relative;
    display: block;
    background: none;
    top: auto;
    left: auto;
    padding: 0;
    right: auto;
    margin: 15px 0;
    .form-row {
      position: relative;
    }
    .comment-img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      position: absolute;
      left: 10px;
      top: 10px;
      &-inner {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          display: block;
        }
      }
    }
    .comment-input {
      textarea {
        padding: 15px 80px;
        height: 70px;
      }

    }
    .comment-submit {
      position: absolute;
      right: 10px;
      bottom: 10px;
      left: auto;
      .btn {
        position: static;
        font-size: 0;
        width: auto;
        max-width: auto;
        padding: 10px;
        &:before {
          font-size: 18px;
        }
      }

    }
  }
}


.img-send {
  position: absolute;
  right: 15px;
  bottom: 15px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #fff;
  span {
    display: block;
    width: 100%;
    position: absolute;
    top: 50%;
    text-align: center;
    transform: translateY(-50%);
  }
  input {
    opacity: 0;
    cursor: pointer;
    padding: 0px;
    margin: 0px;
    border: none;
    width: 50px;
    height: 50px;
    top: 0;
    /* margin-left: -450px; */
    position: absolute;
    z-index: 100;
  }
}





