  }
  .form-row {
    //width: 50%;
    //float: left;
    //padding-left: 10px;
    &.btn-row {
      width: 100%;
      //padding-left: 10px;
      float: none;
    }
  }

}
@media (max-width: 680px) {
  .form {
    .btn.send {
      width: 100%;
      float: none;
    }
  }
}
