@import '../scss/_mixins&includes.scss';

@media (max-width: 980px) {
  .friend-list {
    &-item {
      //width: 33.33%;
    }
  }

}

@media (max-width: 680px) {

  .user-posts,
  .user-comments {
    padding: 0;
  }

  .post-left,
  .post-right,
  .comment-left,
  .comment-right  {
    width: 100%;
    display: block;
  }

  .post-left,
  .comment-left {
    margin-bottom: 0;
  }

  .post-right {
    text-align: left;
    width: 100%;
    padding-left: 65px;
  }

  .post-author,
  .author-img {
    vertical-align: top !important;
  }

  .post-tags-list {
    margin-top: -30px;
  }

  .post-middle,
  .post-bottom,
  .comment-middle {
    padding: 0;
  }
  .post-bottom {
    margin: 0;
  }

  .sorting-list {
    li {
      width: 33.33%;

    }
    &.type {
       li {
         //display: block;
         //width: 100%;
         //margin-left: 0;
         //margin-bottom: 15px;
         &:last-child {
           //margin-bottom: 10px;
         }
       }
    }
    &.series {
      display: flex;
      flex-wrap: wrap;
      li {
        display: flex;
        width: 33.33%;
        margin-bottom: 15px;
        &:nth-last-child(-n+3) {
          margin-bottom: 0;
        }
        a {
          display: block;
          width: 100%;
          align-items: center;
          justify-content: center;
          display: flex;
        }
      }
    }
  }
  .table {
    &.sorting-table {
      .product-row {
        &.visible {
          .long-description {
            display: block;
          }
        }
        tbody {
          display: block;
        }
      }
      .img-upload,
      .product-rate {
        display: block;
      }
      .img-upload {
        margin: 0 auto 10px;
      }
      .product-rate {
        margin: 0 auto;
        text-align: center;
      }
    }
    .short-description {
      p {
        padding: 0;
      }
    }
    .long-description {
      td {
        width: 100%;
        display: block;
        text-align: left;
      }
      p {
        padding: 0;
      }
    }
  }
  .friend-img-holder,
  .category-img-holder {
    width: 75px;
    height: 75px;
  }
  .friend-list,
  .category-list {
    &-item {
      width: 25%;
    }
  }
  .category-list {
    &-item {
      width: 50%;
    }
  }
  .category-img-holder {
    width: 230px;
    height: 230px;
  }
  .category-info {
    &.full {
      .category-info-list {
        li {
          width: 50%;
        }
        a {
          width: 50%;
          height: 50%;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }



}


@media (max-width: 560px) {
  .user-tabs {
    .user-tab-list {
      li {
        width: 50%;
      }
    }
  }
  .user-about {
    &_top {
      .user-top-list {
        li {
          width: 70%;
          &:first-child,
          &:last-child {
            width: 15%;
          }
        }
      }
    }
  }
  .user-middle-list {
    li {
      width: 50%;

    }
  }
  .char-table {
    display: block;
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }
    td {
      width: 100%;
      padding-left: 50%;
    }
    thead {
      tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
      }
    }
    tbody {
      td {
        width: 100%;
        padding-left: 50%;
        padding-top: 10px;
        text-align: left;
        padding-bottom: 10px;
      }
    }
    td {
      /* Behave  like a "row" */
      border: none;
      border-bottom: 1px solid #9894ff;
      position: relative;
      padding-left: 50%;
    }
    td:before {
      /* Now like a table header */
      position: absolute;
      /* Top/left values mimic padding */
      top: 50%;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
      transform: translateY(-50%);
    }
    /*
     Label the data
     */
    td:nth-of-type(1):before {
      content: "Материал";
    }
    td:nth-of-type(2):before {
      content: "Производитель";
    }
    td:nth-of-type(3):before {
      content: "Размер";
    }
    td:nth-of-type(4):before {
      content: "Плотность";
    }
  }
  .vacancies-holder {
    width: 100%;
  }
  .vacancies-left,
  .vacancies-right {
    width: 100%;
    float: none;
  }
  .vacancies-left {
    text-align: center;
    .img {
      margin: 0 auto 10px;
    }
  }
  .vacancies-right {
    margin-left: 0;
  }
  .friend-list,
  .category-list {
    &-item {
      width: 25%;
    }
  }
  .category-list {
    &-item {
      width: 50%;
    }
  }
  .category-info {
    &.full {
      .category-info-list {
        li {
          width: 50%;
        }
        a {
          width: 150px;
          height: 150px;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
  .category-img-holder {
    width: 150px;
    height: 150px;
  }



  .table {
    &.basket {
      display: block;
      thead,
      tbody,
      th,
      td,
      tr {
        display: block;
      }
      td {
        width: 100% !important;
        padding-left: 50%;
      }
      thead {
        tr {
          position: absolute;
          top: -9999px;
          left: -9999px;
        }
      }
      tbody {
        td {
          width: 100%;
          padding: 10px 0 10px 50% !important;
          text-align: left;
        }
      }
      tfoot {
        display: block;
      }
      td {
        /* Behave  like a "row" */
        border: none;
        border-bottom: 1px solid #9894ff;
        position: relative;
        padding-left: 50%;
      }
      td:before {
        /* Now like a table header */
        position: absolute;
        /* Top/left values mimic padding */
        top: 50%;
        left: 6px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
        transform: translateY(-50%);
        text-align: left;
      }
      /*
       Label the data
       */
      td:nth-of-type(1):before {
        content: "Товар";
      }
      td:nth-of-type(2):before {
        content: "Цена";
      }
      td:nth-of-type(3):before {
        content: "Кол-во";
      }
      td:nth-of-type(4):before {
        content: "сумма";
      }
      .img-holder {
        margin: 0 auto 10px;
        display: block;
      }
      .img-text {
        padding: 0;
        display: block;
        text-align: center;
      }
    }
  }
}


@media (max-width: 480px) {
  .sorting-list {
    display: block;
    &.type {
       li {
         display: block;
         width: 100%;
         margin-left: 0;
         margin-bottom: 15px;
         &:last-child {
            margin-bottom: 10px;
         }
       }
    }
    &.series {
      display: flex;
      flex-wrap: wrap;
      li {
        display: flex;
        width: 50%;
        margin-bottom: 15px;
        &:nth-last-child(-n+3) {
          margin-bottom: 15px;
        }
      }
    }
  }
  .friend-list  {
    &-item {
      width: 33.33%;
    }
  }
  .friend {
    &-sername {
      padding-left: 0;
    }
  }
  .category-info {
    &.full {
      .category-info-list {
        a {
          width: 100px;
          height: 100px;

        }
      }
    }
  }
  .category-img-holder {
    width: 100px;
    height: 100px;
  }

}