@import '../scss/_mixins&includes.scss';
* {
	box-sizing: border-box;
}
html {
	height: auto;
}
body {
	background: #4d4d51 url('../img/body-bg.jpg');
	background-size: cover;
	line-height: 14px;
	font-size: 16px;
	font-family: 'Arial', 'Arial', 'Helvetica', sans-serif;
	color: #ffffff;
	overflow-x: hidden;
	min-width: 320px;
	&.opened {
		overflow-y: hidden;
	}
}
h1, h2, h3, h4, h5, h6 {
	//color: $white_color;
	//font-family: $roboto_r;
