%defaultList {
	margin: 0 0 35px 11px !important;
	li {
		position: relative;
		padding-left: 21px !important;
		//font-size: $sm_fs;
		line-height: 24px;
		&:before {
			content: "";
			width: 7px;
			height: 7px;
			//background: $green_bg;
			position: absolute;
			left: 0;
			top: 9px;
			border-radius: 0;
		}
	}
}



ul,
ol {
	list-style: none;
	li {
		list-style: none;
	}
}

.title {
	font-size: 18px;
	padding: 10px 0;
}




