@import '../scss/_variables.scss';
@import '../scss/_sprite-icons.scss';
@import '../scss/_mixins&includes.scss';

/* ALL STYLES */


/* transition */

.transition-all,
a,
.tr,
[class^="btn-"],
[class*=" btn-"],
.wrap-input {
    transition: all .5s ease-in-out;
}


/* transition end */


/* clearfix */

.clearfix,
.buttons,
.bxslider,
.cfx,
[class*="-columns"],
form {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}


/* clearfix end */


/* reset-list */

[class$="-list"],
[class*="-list "],
[class$="-list"] > li,
[class*="-list "] > li {
    background: none;
    border: 0;
    list-style: none;
    margin: 0;
    padding: 0;
}


/* reset-list end */


/* inline block */

.inline-b,
[class^="btn-"],
[class*=" btn-"],
[class^="icon-"],
[class*=" icon-"] {
    vertical-align: top;
    display: -moz-inline-stack;
    display: inline-block;
    position: relative;
}


/* inline block end */

/* clear-list */

.clear-list {
    & li:before {
        display: none;
    }
}

/* clear-list end */


/* columns */

.columns {
    @extend .clearfix;
}

.col-50 {
    width: 50%;
    float: left;
}

.col-33 {
    width: 33.33%;
    float: left;
}

.col-20 {
    width: 20%;
    float: left;
}

.col-25 {
    float: left;
    width: 25%;
}


/* columns end */


/* inline-block */


.inline-b {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: top;
}

.inline-m {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: middle;
}

.inline-bt {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: bottom;
}


/* inline-block end */

.bold {
    font-weight: bold;
}

.medium {
    font-weight: 500;
}

.medium-b {
    font-weight: 600;
}

.light {
    font-weight: lighter;
}

.block {
    display: block;
}


.pull-left {
    float: left;
}

.pull-right {
    float: right;
}


/* other reusable styles */

.nowrap {
    white-space: nowrap;
}

ol {
    counter-reset: headings;
}

ol,
ul {
    margin: 10px 0;
    padding: 0;
    list-style: none;
    li {
        position: relative;
        padding: 0 0 0 30px;
        margin: 0 0 13px;
        &:before {
            position: absolute;
        }
    }
}

ol li:before {
    // color: $blue_color;
    // font-family: $romul;
    counter-increment: headings;
    content: counter(headings, decimal);
    left: 2px;
    top: 0;
    font-size: 144px;
}

ul li:before {
    // background: $main_color;
    border-radius: 50%;
    content: none;
    width: 5px;
    height: 5px;
    left: 2px;
    top: 7px;
}


/* other reusable styles end */


/* general styles */

body *,
body *:after,
body *:before {
    box-sizing: border-box;
}


/* DEFAULT TABLE STYLE */

.table {}


/* general styles end */

.title {
  margin-bottom: 10px;
}

.user-main-info {
    .user-photo-edit {
        display: none;
    }
    .info-holder {
        input {
            display: none;
        }
    }
    .btn {
        &.save {
