        }
    }
    &.active {
        .user-photo,
        .user-name,
        .user-surname,
        .btn.change {
            display: none;
        }
        .user-photo-edit {
            display: block;
        }
        .info-holder {
            input {
                display: block;
            }
        }
        .btn {
            &.save {
            display: block;
            }
        }
