    }
}



.user-photo-edit {
  position: relative;
  width: 150px;
  height: 150px;
  margin: 0 auto 15px;
  border-radius: 50%;
  border: 1px solid #fff;
  span {
    display: block;
    width: 100%;
    height: 150px;
    vertical-align: middle;
    text-align: center;
    display: flex;
    padding: 0;
    align-items: center;
    line-height: 27px;
  }
  input {
    opacity: 0;
    font-size: 40px;
    cursor: pointer;
    padding: 0px;
    margin: 0px;
    border: none;
    width: 150px;
    height: 150px;
    top: 0;
    left: 0;
    /* margin-left: -450px; */
    position: absolute;
  }
}


//user profile about
.user-about {
    &_top {
        margin-bottom: 15px;
        .user-top-list {
            font-size: 0;
            li {
                width: 33.33%;
                vertical-align: middle;
                display: inline-block;
                font-size: 28px;
                .link {
                    span {
                        font-size: 28px;
                    }
                }
                &:first-child {
                    text-align: right;
                    & + li {
                        text-align: center;
                    }
                }
                &:last-child {
                    text-align: left;
                }
            }
        }
    }
    &_middle {
        padding: 0;
        margin-bottom: 10px;
        background: rgba(#fff, 0.1);

    }
    &_edit {
      @exted .cfx;
    }
}

.user-info-img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 0 auto 15px;
    img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: block;
    }
}

.user-name {
    text-align: center;
    text-transform: capitalize;
    font-size: 18px;
    color: #fff;
}

.user-middle-list {
    margin: 0;
    font-size: 0;
    li {
        padding: 10px 0;
        display: inline-block;
        vertical-align: middle;
        width: 25%;
        text-align: center;
        vertical-align: top;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.title-text {
    display: block;
    margin-bottom: 10px;
    text-transform: capitalize;
    color: #bdbdbd;
    font-size: 16px;
}

.count-text {
    display: block;
    text-transform: uppercase;
    color: #ffffff;
    font-size: 18px;
}

.user-bottom-list {
    li {
        display: block;
        padding: 20px 0 20px 10px;
        border-bottom: 2px solid #434442;
        &:last-child {
            border-bottom: none;
        }
    }
    .about-title {
        display: block;
        margin-bottom: 10px;
        font-size: 16px;
        color: #bdbdbd;
        text-transform: uppercase;
    }
    .about-info {
        display: block;
        text-transform: capitalize;
        color: #ffffff;
    }
    a {
        display: block;
        color: #ffffff;
    }
}






//colors
$white: #fff;
$color_abbey_approx: #4c4c56;
$color_portage_approx: #9894ff;

.user-tabs,
.product-tabs {
  > div {
    padding-top: 10px;
  }
  .user-tab-list,
  .product-tab-list {
