    padding: 0;
    width: 100%;
    background: rgba($white, 0.1);
    margin: 15px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: stretch;
    align-items: stretch;
    &:after {
      content: "";
      display: block;
      clear: both;
      height: 5px;
    }
    li {
      margin: 0;
      padding: 0;
      cursor: pointer;
      text-align: center;
      padding: 10px 15px;
      background: $color_abbey_approx;
      color: $white;
      width: 25%;
