      justify-content: space-around;
      align-content: stretch;
      align-items: center;
      &.active {
        background: $color_portage_approx;
        color: $white;
        &:hover {
          background: $color_portage_approx;
          color: $white;
        }
      }
      &:hover {
        background: $color_portage_approx;
      }
    }
  }
}
.tab-title {
  display: block;
  text-transform: capitalize;
}

.product-tabs {
