    &.visible {
      display: block;
    }
  }
}



.order {
  padding: 15px 0;
  p {
    text-transform: capitalize;
    font-size: 20px;
    margin-bottom: 10px;
  }
}

.sorting {
  &-list {
    font-size: 0;
    margin-bottom: 15px;
    margin-left: -15px;
    li {
      @extend .inline-m;
      padding-left: 15px;
      font-size: 18px;
      text-transform: capitalize;
      text-align: center;
      line-height:  18px;
      &:first-child {
        margin-left: 0;
      }
    }
  }
  &-title {
    font-size: 20px;
    margin-bottom: 20px;
    text-transform: capitalize;
  }
  .radio {
    &-row {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
      label {
        font-size: 18px;
        text-transform: capitalize;
      }
    }
  }
}

.sorting-table,
.table.basket {
  thead {
    td,
    th {
      padding: 10px 10px;
      text-transform: capitalize;
      font-size: 18px;
      line-height: 18px;
    }
  }
  tfoot {
    td {
      padding: 10px 0;
    }
  }
  .col-price {
    text-align: center;
    width: 150px;
  }
  .product-row {
    position: relative;
    & > td {
      position: relative;
    }
    td {
      padding: 10px 0;
      border-bottom: 1px solid #434442;
    }
    &.visible {
        .long-description {
            display: table-row;
        }
    }
  }
  .short-description {
    td {
      overflow: hidden;
      border-bottom: none;
    }
    .img-holder {
      @extend .inline-m;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      margin-bottom: 15px;
      a {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 50%;
      }
      img {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 50%;
      }
    }
    p {
      @extend .inline-m;
      padding: 0 15px;
    }
  }
  .long-description {
    display: none;
    td {
      border-bottom: none;
      padding: 15px 0;
    }
  }
  .count-text,
  .input.count {
    @extend .inline-m;
  }
  .count-text {
    font-size: 16px;
    text-transform: capitalize;
  }
  .input.count {
    width: 100px;
    border: 2px solid #434442;
    padding: 5px 5px;
    margin-top: 0px;
    margin-left: 10px;
    &:focus {
      border-color: #8c88ff;
    }
  }
  .img-upload {
    position: relative;
    width: 100px;
    height: 100px;
    margin: 0 0 15px;
    border-radius: 50%;
    border: 1px solid #fff;
    span {
      display: block;
      width: 100%;
      height: 100px;
      vertical-align: middle;
      text-align: center;
      padding: 40% 0 0 0;
    }
    input {
      opacity: 0;
      font-size: 40px;
      cursor: pointer;
      padding: 0px;
      margin: 0px;
      border: none;
      width: 100px;
      height: 100px;
      top: 0;
      /* margin-left: -450px; */
      position: absolute;
    }
  }
  .img-upload,
  .product-rate {
    @extend .inline-m;
  }
  .product-rate {
    margin-left: 15px;
  }
  .col-price {
    position: relative;
  }
}

.table {
  &.basket {
    th,
    td {
      padding: 10px 0;
      border-bottom: 1px solid #434442;
      border-right: 1px solid #434442;
    }
    .col-summory,
    .col-del {
      border-right: none;
    }
    .col-price,
    .col-count,
    .col-summory,
    .col-del {
      text-align: center;
    }
    .input {
      display: inline-block;
    }
    .img-holder {
      @extend .inline-m;
      width: 100px;
      height: 100px;
      border-radius: 50%;
      margin-bottom: 0px;
      &-inner {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 50%;
      }
      img {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 50%;
      }
    }
    .img-text {
      @extend .inline-m;
      padding-left: 15px;
      font-size: 16px;
      text-transform: capitalize;
    }
  }
  th.col-total {
    text-align: right;
    border-right: none;
    padding-right: 10px;
    .total {
      font-size: 20px;
      color: #8c88ff;
      padding-left: 15px;
    }
  }
}





.product-review {
    font-size: 18px;
    display: block;
}

.order-success,
.add-success {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: #4c4c56;
  text-align: center;
  z-index: 100;
  display: none;
  &.visible {
    display: block;
    position: absolute;
  }
  &-inner {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 150px;
    width: 100%;
    font-size: 18px;
  }
  .order-list {
    font-size: 0;
    margin: 10px 0 0 -10px;
    li {
      @extend .inline-m;
      margin: 20px 0 0 10px;
      width: 50%;
    }
    .link {
      text-transform: capitalize;
      @extend .sorting;
      @extend .sorting.active;

    }
  }
}

.add-success {
  &-inner {
    height: auto;
  }
}

