//about us style
.about-us-holder {
  padding: 15px 15px;
  &-inner {
    @extend .cfx;
  }
  h1 {
    color: #62d7c8;
    font-size: 24px;
    margin-bottom: 20px;
  }
  img {
    padding-bottom: 15px;
  }
  .pull-left,
  .pull-right {

  }
  .pull-left {
    padding-right: 15px;
  }
  .pull-right {
    padding-left: 15px;
  }
  .link {
    color: #8c88ff;
    font-size: 16px;
  }
  dl {
    text-align: right;
  }
  dt {
    font-weight: bold;
    color: #fcb465;
    margin-bottom: 5px;
    text-transform: capitalize;
  }
  dd {
    margin-bottom: 10px;
  }
}



//friend list style
.friend-list-holder,
.category-list-holder {
  padding: 25px 0;
}
.friend-list,
.category-list  {
  margin: -30px 0 0 -30px;
  position: relative;
  @extend .cfx;
  font-size: 0;
  &-item {
    position: static;
    @extend .inline-b;
    font-size: 16px;
    padding: 30px 0 0 30px !important;
    width: 25%;
    &.active {
      .friend-img,
      .category-img {
        border-color: #fcb465;
      }
      .friend-info.full,
      .category-info.full {
        display: block;
      }
    }
  }
}


.friend-info,
.category-info {
  &.short {
    margin-bottom: 15px;
  }
  &.full {
    display: block;
    position: absolute;
    left: 15px;
    right: 0;
    width: 100%;
    z-index: 100;
    background: #4c4c56;
    padding: 15px;
    .friend-info-list,
    .category-info-list {
      font-size: 0;
      margin-top: -10px;
      margin-left: -10px;
      li {
        @extend .inline-b;
        font-size: 16px;
        width: 100%;
        padding-top: 10px;
      }
      .btn {
        font-size: 16px;
        display: block;
        width: 100%;
        text-align: center;
      }
      .link {
        font-size: 16px;
      }
    }
    .category-info-list {
      li {
        width: 25%;
        padding-top: 10px;
        padding-left: 10px;
        display: inline-block;
        border-radius: 50%;
      }
      a {
        display: block;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        margin: 0 auto;
        img {
          width: 150px;
          height: 150px;
          display: block;
          border-radius: 50%;
        }
      }
    }
    display: none;
  }
}

.friend-img,
.category-img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  display: block;
  border: 3px solid transparent;
  &-holder {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin: 0 auto 10px;
    &_inner {
      border-radius: 50%;
      width: 100%;
      height: 100%;
    }
  }
}

.friend,
.category {
  &-name-holder {
    text-align: center;
    font-size: 0;
  }
  &-name,
  &-sername {
    @extend .inline-b;
    font-size: 14px;
    text-transform: capitalize;
  }
  &-sername {
    padding-left: 5px;
  }
}

//basket
.basket-holder {
  .title {
    padding: 15px 0;
  }
  .btn.order {
    display: inline-block;
    width: 200px;
    text-align: center;
    &.active {
      background: #fcb465 !important;
    }
  }
}

.total-holder {
  @exted .cfx;
  margin-bottom: 10px;

}


.post-holder {
  &.inner {
    .title {
      font-size: 18px;
      padding: 10px 0;
    }
    p {
      margin-bottom: 10px;
    }
    a {
      display: inline-block;
      margin-bottom: 10px;
    }
  }
}

.post-comment {
  &-img,
  &-text {
    @extend .inline-b;
  }
  &-img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 10px;
    &-inner {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        display: block;
      }
    }
  }
  &s {
    &-holder {
      padding: 10px 0;
      margin: 10px 0;
      border-top: 1px solid #fcb465;
    }
    &-list {

    }
    &-item {
      border-bottom: 1px solid #62d7c8 !important;
      padding-bottom: 10px !important;
      margin-bottom: 10px !important;

      &:last-child {
        border-bottom: none !important;
        padding-bottom: 0 !important;
        margin-bottom: 0 !important;
      }
    }
  }
}


.messages-holder {
  .title {
    font-size: 18px;
    padding: 10px 0;
  }
}

.message {
  &s-list {

  }
  &-item {
    position: relative;
    border-bottom: 1px solid #62d7c8 !important;
    padding-bottom: 20px !important;
    margin-bottom: 20px !important;
    &:last-child {
      border-bottom: none !important;
      padding-bottom: 0 !important;
      margin-bottom: 0 !important;
    }
    .message-remove {
      position: absolute;
      right: 10px;
      top: 0;
    }
  }
  &-user {
    &-img,
    &-text {
      @extend .inline-b;
    }
    &-img {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      margin-right: 10px;
      &-inner {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          display: block;
        }
      }
    }
    &-text {
      padding-right: 20px;
    }
  }
}

.user {
  &-title {
    font-weight: bold;
    display: block;
    margin-bottom: 10px;
  }
}